.nave {
	--main: #003b6f;
	--darker: #002d56;
	z-index: -1;
}

.nave::before {
	width: 20px;
	height: 20px;
	content: '';
	background: #000;
	display: block;
	position: absolute;
	top: -50px;
	left: 91px;
	border: 3px solid var(--main);
	border-radius: 50% 50% 0 0;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	background-image: -o-linear-gradient(right,
			transparent 6px,
			var(--main) 6px,
			var(--main) 8px,
			transparent 8px),
		-o-linear-gradient(top, transparent 7px, var(--main) 7px, var(--main) 9px, transparent 9px),
		-o-radial-gradient(yellow, white);
	background-image: linear-gradient(to left,
			transparent 6px,
			var(--main) 6px,
			var(--main) 8px,
			transparent 8px),
		linear-gradient(to bottom,
			transparent 7px,
			var(--main) 7px,
			var(--main) 9px,
			transparent 9px),
		radial-gradient(yellow, white);

	-webkit-box-shadow: 0px 0px 40px 0px gold;

	box-shadow: 0px 0px 40px 0px gold;
}

.nave {
	height: 300px;
	width: 200px;
	background: var(--main);

	-webkit-box-shadow: 0px 2px 6px 0px black;

	box-shadow: 0px 2px 6px 0px black;

	background-image: -o-linear-gradient(bottom,
			rgba(0, 0, 0, 0.15) 10px,
			rgba(255, 255, 255, 0.1) 10px,
			rgba(255, 255, 255, 0.1) 20px,
			transparent 20px)
		/* Feet */
		,
		-o-linear-gradient(left,
			var(--main) 40px,
			transparent 40px,
			transparent 95px,
			var(--main) 95px,
			var(--main) 105px,
			transparent 105px,
			transparent 160px,
			var(--main) 160px)
		/* Panel Cutout */
		,
		-o-linear-gradient(bottom,
			transparent 30px,
			var(--darker) 30px,
			var(--darker) 90px,
			transparent 90px,
			transparent 100px,
			var(--darker) 100px,
			var(--darker) 160px,
			transparent 160px,
			transparent 170px,
			var(--darker) 170px,
			var(--darker) 220px,
			transparent 220px)
		/* Panels */
		,
		-o-linear-gradient(left,
			var(--main) 40px,
			transparent 40px,
			transparent 56.5px,
			var(--main) 56.5px,
			var(--main) 59.5px,
			transparent 59.5px,
			transparent 75.5px,
			var(--main) 75.5px,
			var(--main) 78.5px,
			transparent 78.5px,
			transparent 95px,
			var(--main) 95px,
			var(--main) 105px,
			transparent 105px,
			transparent 121.5px,
			var(--main) 121.5px,
			var(--main) 124.5px,
			transparent 124.5px,
			transparent 140.5px,
			var(--main) 140.5px,
			var(--main) 143.5px,
			transparent 143.5px,
			transparent 160px,
			var(--main) 160px)
		/* Window Cutout */
		,
		-o-linear-gradient(bottom,
			transparent 254px,
			var(--main) 254px,
			var(--main) 256px,
			transparent 256px)
		/* Window Vertical Stripe */
		,
		-o-linear-gradient(bottom,
			transparent 230px,
			rgba(150, 150, 150, 1) 230px,
			rgba(255, 255, 255, 1) 280px,
			transparent 280px)
		/* Window */
	;

	background-image: linear-gradient(to top,
			rgba(0, 0, 0, 0.15) 10px,
			rgba(255, 255, 255, 0.1) 10px,
			rgba(255, 255, 255, 0.1) 20px,
			transparent 20px)
		/* Feet */
		,
		linear-gradient(to right,
			var(--main) 40px,
			transparent 40px,
			transparent 95px,
			var(--main) 95px,
			var(--main) 105px,
			transparent 105px,
			transparent 160px,
			var(--main) 160px)
		/* Panel Cutout */
		,
		linear-gradient(to top,
			transparent 30px,
			var(--darker) 30px,
			var(--darker) 90px,
			transparent 90px,
			transparent 100px,
			var(--darker) 100px,
			var(--darker) 160px,
			transparent 160px,
			transparent 170px,
			var(--darker) 170px,
			var(--darker) 220px,
			transparent 220px)
		/* Panels */
		,
		linear-gradient(to right,
			var(--main) 40px,
			transparent 40px,
			transparent 56.5px,
			var(--main) 56.5px,
			var(--main) 59.5px,
			transparent 59.5px,
			transparent 75.5px,
			var(--main) 75.5px,
			var(--main) 78.5px,
			transparent 78.5px,
			transparent 95px,
			var(--main) 95px,
			var(--main) 105px,
			transparent 105px,
			transparent 121.5px,
			var(--main) 121.5px,
			var(--main) 124.5px,
			transparent 124.5px,
			transparent 140.5px,
			var(--main) 140.5px,
			var(--main) 143.5px,
			transparent 143.5px,
			transparent 160px,
			var(--main) 160px)
		/* Window Cutout */
		,
		linear-gradient(to top,
			transparent 254px,
			var(--main) 254px,
			var(--main) 256px,
			transparent 256px)
		/* Window Vertical Stripe */
		,
		linear-gradient(to top,
			transparent 230px,
			rgba(150, 150, 150, 1) 230px,
			rgba(255, 255, 255, 1) 280px,
			transparent 280px)
		/* Window */
	;

	position: absolute;

	left: calc(50% - 100px);
	top: calc(50% - 125px);
}

.nave::after {
	content: 'POLICE BOX';
	width: 190px;
	height: 20px;
	background-color: #222;
	display: block;
	color: white;
	font-family: monospace;
	font-size: 1.5em;
	line-height: 20px;
	text-align: center;
	position: absolute;
	left: 5px;
	top: -10px;

	-webkit-box-shadow: 0px 0px 0px 6px var(--main), 0px 0px 6px 6px black,
		0px -12.5px 0px 1px var(--darker), 0px -12.5px 6px 1px black,
		0px -25px 0px -4px var(--main), 0px -25px 6px -4px black;

	box-shadow: 0px 0px 0px 6px var(--main), 0px 0px 6px 6px black,
		0px -12.5px 0px 1px var(--darker), 0px -12.5px 6px 1px black,
		0px -25px 0px -4px var(--main), 0px -25px 6px -4px black;
}